import React from "react"
import PropTypes from "prop-types"
import { faCaretDown, faCaretUp } from "@fortawesome/pro-solid-svg-icons"
import { FontAwesomeIcon as FA } from "@fortawesome/react-fontawesome"
import { DomsStyled } from "./Doms.styled"
import Dom from "./Dom"
import { H5Styled } from "../../containers/Domains/Domains.styled"
// import { astrings_shuffle_last, astrings_shuffle_last_strict } from "universal-common-scripts/functions/astrings"
import { str_capitalize } from "universal-common-scripts/functions/strings"
// import { astrings_shuffle_first_last_strict } from "universal-common-scripts/functions/astrings"
// import asort_by_rating from "universal-common-scripts/functions/asort_strings/asort_by_rating_and_position"
// import asort_by_rating_and_position from "universal-common-scripts/functions/asort_strings/asort_by_rating_and_position"

let list_title = function (listname, one_list, only_available) {
  // custom capitalize / edit title
  if (listname === "tld") {
    return "TLD Suggestions"
  } else if (listname === "exact") {
    return "Exact match + Generic TLDs"
  } else if (listname === "word hack") {
    return "Brandable"
  } else if (listname === "name") {
    if (one_list) {
      if (only_available) {
        return "Available Suggestions"
      } else {
        return "All Suggestions"
      }
    } else {
      return "Suggestions"
    }
  } else if (listname === "com") {
    if (only_available) {
      return "Available .com"
    } else {
      return ".com"
    }
  } else if (listname === "gdomains") {
    if (only_available) {
      return "Available Generic"
    } else {
      return "Generic"
    }
  } else if (listname === "all") {
    return "Suggestions"
  }
  // capitalize all by default
  return str_capitalize(listname) + "s"
}
let list_limit = function (listname, one_list) {
  // defaults for each category
  // Note: (one_list ? 6 : 9)
  // First "?" number is how many to show all at once on top
  // Second ":" number is how many to scatter in ever other few
  let limit = 99
  if (listname === "exact") {
    limit = one_list ? 0 : 6
  } else if (listname === "com") {
    limit = one_list ? 0 : 6
  } else if (listname === "tld") {
    limit = one_list ? 0 : 6
  } else if (listname === "word hack") {
    limit = one_list ? 0 : 3
  } else if (listname === "phrase hack") {
    limit = one_list ? 0 : 3
  } else if (listname === "name") {
    limit = one_list ? 0 : 999
  } else if (listname === "all") {
    limit = 999
  }
  // show all available
  // limit = Math.max(limit, available)
  // adjust to screen size
  if (typeof window === "object") {
    /*
     * adjust to screen size
     */
    // 3 columns
    if (window.innerWidth > 1299) {
      if (limit % 3) {
        limit += 3 - (limit % 3)
      }
    }
    // 2 columns
    else if (window.innerWidth > 899) {
      if (limit === 9) {
        limit = 6
      } else if (limit % 2) {
        limit += 2 - (limit % 2)
      }
    }
    // 1 column
    else {
      if (limit === 9) {
        limit = 3
      }
    }
  }
  // done
  return limit
}

class Doms extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      // ['unlimit_'+listname]: {boolean}
    }
  }
  render() {
    /*
     * User "container" state/props/methods!
     * This "child" component is to simplify container's render() logic.
     */
    let {
      ui = {},
      suggestions_keys = [],
      suggestions_lists = {},
      domains_availability = {},
      input_tld,
      input_str,
      input_spellchecked,
      tlds_checked = [],
      input_words_arr = []
    } = this.props

    /*
     * validate
     */
    if (!suggestions_lists || !Object.keys(suggestions_lists).length) {
      return (
        <DomsStyled className="DomsStyled">
          <div className="content">
            <p>&nbsp;</p>
            <p>loading results......</p>
          </div>
        </DomsStyled>
      )
    }

    /*
     * View
     */
    // if (typeof window === "object" && window.time1log) window.time1log("render Doms/Doms")
    // console.log(suggestions_lists)
    return (
      <DomsStyled className={"DomsStyled "}>
        {/*
         * Results
         */}
        {suggestions_keys.map((listname) => {
          let list = suggestions_lists[listname] || []
          let limit = list_limit(listname, !!ui.show_one_list)
          let label = list_title(listname, !!ui.show_one_list, !ui.show_unavailable)

          /*
           * Limit number of results
           * Show full list if user clicked to expand it,
           * or limit the list items to maximum number.
           */
          if (this.state["unlimit_" + listname]) {
            limit = 1000
          }

          /*
           * more
           */
          let MoreLink = null
          if (list.length > limit) {
            MoreLink = (
              <div className="dom_name unlimit">
                <span
                  onClick={() => {
                    this.setState({
                      ["unlimit_" + listname]: true
                    })
                  }}
                >
                  more &thinsp;
                  <FA className="fa-angle-down" icon={faCaretDown} />
                </span>
              </div>
            )
          } else if (this.state["unlimit_" + listname] === true) {
            MoreLink = (
              <div className="dom_name unlimit">
                <span
                  onClick={() => {
                    this.setState({
                      ["unlimit_" + listname]: false
                    })
                  }}
                >
                  less &thinsp;
                  <FA className="fa-angle-up" icon={faCaretUp} />
                </span>
              </div>
            )
          }

          /*
           * limit
           */
          list = list.slice(0, limit)

          /*
           * view
           */
          return list.length ? (
            <div key={listname} className={"doms_group"}>
              {" "}
              <H5Styled className="doms_title">{label}</H5Styled>
              <div className="doms_content">
                {/*
                 * Results
                 */}
                {list.map((phraseString, pi) => {
                  return (
                    <Dom
                      key={phraseString + pi}
                      listname={listname}
                      phraseString={phraseString}
                      domains_availability={domains_availability}
                      input_tld={input_tld}
                      input_str={input_str}
                      input_spellchecked={input_spellchecked}
                      input_words_arr={input_words_arr}
                    />
                  )
                })}
                {/*
                 * Link to "show more"
                 */}
                {MoreLink}
              </div>
            </div>
          ) : null
        })}
      </DomsStyled>
    )
  }
}

Doms.propTypes = {
  suggestions_lists: PropTypes.object,
  domains_availability: PropTypes.object
}

export default Doms
