import Tlds from "./Tlds"
import { ShowLinkStyled, ColorsStyled, _, __, ___ } from "./Domains.styled"
import { FontAwesomeIcon as FA } from "@fortawesome/react-fontawesome"
import Search from "../../components/Search"
import { faStar as faStarSolid, faBolt, faCaretDown } from "@fortawesome/pro-solid-svg-icons"
import { faTimes, faCode } from "@fortawesome/pro-regular-svg-icons"
